.container{
  padding: 0;
  margin: 0;
}
body{
  background-color: #FFEDD3;
}

.navbar{
  font-size: 18px;
  color: #FF5C58;
  background-color: #FCD2D1;
  padding: 6px;
  display: flex;
  justify-content: space-between;
  padding-left: 40px;
  padding-right: 40px;
}
i{
  padding-left: 10px;
  padding-right: 10px;
}

.searchbar{
  display: flex;
  justify-content: center;
  margin-top: 10px;
}

form {
  background-color: #FCD2D1;
  width: 80%;
  height: 44px;
  border-radius: 5px;
  display: flex;
  align-items: center;
}
form input{
  all: unset;
  width: 100%;
  height: 100%;
  padding: 6px 10px;
}

form input::placeholder {
  color: #FF5C58;
  opacity: 0.7; 
}
form button{
  all: unset;
  cursor: pointer;
  
}
form button i{
  color: #FF5C58;
  fill: currentColor;
}
.likes span{
  font-size: 35px;
  color: #f83e3b;
}
.likes button{
  all: unset;
  cursor: pointer;
}
.likeicon{
  color:#FF5C58;
}
.music{
  display: flex;
  justify-content: space-between;
  width: 80%;
  margin: 10px auto;
  background-color: #FCD2D1;
  align-items: center;
  padding: 11px 20px;
}

.songdetails{
  padding: 0 20px;
}
.songdetails p{
  padding: 0px;
  margin: 0px;
}
.songdetails .title{
  font-size: 20px;
  color: #f00c0c;
}
.songdetails .subtitle{
  color: #f30c0c9d;
}
.controls{
  display: flex;
  justify-content: space-between;
  background-color: #FE8F8F;
  border-radius: 30px;
  height: 23px;
}
.controls button{
  all: unset;
  cursor: pointer;
}
.controls input{
  color: #FE8F8F;
  width: 300px;
}

.controls span{
  margin: auto;
}
#close{
  margin-left: 10px;
  margin-right: 10px;
}














